.input {
  &-wrapper {
    display: flex;
    width: 100%;
    padding: 20px;
    margin-top: 20px;
    border-radius: 6px;
    background-color: rgba(69, 90, 100, 0.2);
    box-sizing: border-box;
  }

  &-feature {
    display: flex;
    width: 50%;
    padding: 10px;
    flex-direction: column;

    .title {
      display: flex;
      align-items: center;
      color: white;
      height: 50px;
      padding: 0 20px;
      background-color: rgba(255, 255, 255, 0.2);  
    }
  }
}